    -webkit-align-items: flex-start;
            align-items: flex-start;
  }

  .card:hover,
  .card:active {
    height: 600px;
  }

  .card .content {
    width: 100%;
    left: 0;
    padding: 30px;
  }

  .card:hover img,
  .card:active img {
    top: 70%;
    left: 50%;
    height: 320px;
  }
}

.btn__products {
  border: 3px solid #fff;
  border-radius: 0.75rem;
  padding: 0.75rem 1rem;
  transition: all 0.3s ease-in-out;
  position: relative;
  margin-top: 12rem;
}

.btn__products:hover {
  color: black;
  font-weight: bolder;
  background-color: white;
}

.btn__body {
  margin-top: 5rem;
}

